.ai-chat-container {
    transition: opacity 0.3s ease-out, height 0.3s ease-out, transform 0.3s ease-out, box-shadow 0.3s ease-out;

    .ai-chat-frame {
        transition: box-shadow 0.3s ease-out;

        iframe {
            transition: opacity 0.15s ease-out 0.15s;
        }
    }

    .ai-chat-close {
        transition: transform 0.2s ease-out 0.2s, opacity 0.2s ease-out 0.2s;
    }

    // iframe {
